import React, { useEffect } from 'react';
import { Avatar, Button, Card, CardActions, CardContent, Divider, Grid, makeStyles, Menu, MenuItem, Typography } from '@material-ui/core';

import ChevronRightOutlinedIcon from '@material-ui/icons/ChevronRightOutlined';
import MoreHorizOutlinedIcon from '@material-ui/icons/MoreHorizOutlined';

import KeyboardArrowUpOutlinedIcon from '@material-ui/icons/KeyboardArrowUpOutlined';
import KeyboardArrowDownOutlinedIcon from '@material-ui/icons/KeyboardArrowDownOutlined';

import BajajCard from './BajajCard';
import { gridSpacing } from '../../../store/constant';
import { requesicoes } from '../../../utils/services';

const useStyles = makeStyles((theme) => ({
    cardAction: {
        padding: '10px',
        paddingTop: 0,
        justifyContent: 'center'
    },
    primaryLight: {
        color: theme.palette.primary[200],
        cursor: 'pointer'
    },
    divider: {
        marginTop: '12px',
        marginBottom: '12px'
    },
    avatarSuccess: {
        width: '16px',
        height: '16px',
        borderRadius: '5px',
        backgroundColor: theme.palette.success.light,
        color: theme.palette.success.dark,
        marginLeft: '15px'
    },
    successDark: {
        color: theme.palette.success.dark
    },
    avatarError: {
        width: '16px',
        height: '16px',
        borderRadius: '5px',
        backgroundColor: theme.palette.orange.light,
        color: theme.palette.orange.dark,
        marginLeft: '15px'
    },
    errorDark: {
        color: theme.palette.orange.dark
    }
}));

let cursos = []
let aproveitamentos = []

const PopularCard = () => {
    const classes = useStyles();

    const [anchorEl, setAnchorEl] = React.useState(null);

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };
    useEffect(() => {

        init();
    }, [])

    async function init() {

        const [cursoRequest, aprovetamentoRequest]
            = await Promise.all([requesicoes().buscarCurso('?campos=id,nome'), requesicoes().getAproveitamento('?campos=id,estadoFinal,matricula.curso.id,matricula.curso.nome')])
        cursos = cursoRequest.data;
        aproveitamentos = aprovetamentoRequest.data;
        console.log('Curos: ', cursos);
        // console.log('Aproveitamento ', aproveitamentos);

    }
    return (
        <Card>
            <CardContent>
                <Grid container spacing={gridSpacing}>
                    <Grid item xs={12}>
                        <Grid container alignContent="center" justifyContent="space-between">
                            <Grid item>
                                <Typography variant="h4">Aprovações por curso</Typography>
                            </Grid>
                            <Grid item>
                                <MoreHorizOutlinedIcon
                                    fontSize="small"
                                    className={classes.primaryLight}
                                    aria-controls="menu-popular-card"
                                    aria-haspopup="true"
                                    onClick={handleClick}
                                />
                                <Menu
                                    id="menu-popular-card"
                                    anchorEl={anchorEl}
                                    keepMounted
                                    open={Boolean(anchorEl)}
                                    onClose={handleClose}
                                    variant="selectedMenu"
                                    anchorOrigin={{
                                        vertical: 'bottom',
                                        horizontal: 'right'
                                    }}
                                    transformOrigin={{
                                        vertical: 'top',
                                        horizontal: 'right'
                                    }}
                                >
                                    <MenuItem onClick={handleClose}> Esse ano</MenuItem>
                                    <MenuItem onClick={handleClose}> Ano passado</MenuItem>

                                </Menu>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item xs={12}>
                        <BajajCard />
                    </Grid>
                    {/* {cursos.map((curso, index) => {
                        return ( */}
                            <Grid item xs={12} >

                                <Grid container direction="column">
                                    <Grid item>
                                        <Grid container alignItems="center" justifyContent="space-between">
                                            <Grid item>
                                                <Typography variant="subtitle1" color="inherit">
                                                    {/* {curso.nome} */}
                                                </Typography>
                                            </Grid>
                                            <Grid item>
                                                <Grid container alignItems="center" justifyContent="space-between">
                                                    <Grid item>
                                                        <Typography variant="subtitle1" color="inherit">
                                                            1839.00
                                                        </Typography>
                                                    </Grid>
                                                    <Grid item>
                                                        <Avatar variant="rounded" className={classes.avatarSuccess}>
                                                            <KeyboardArrowUpOutlinedIcon fontSize="small" color="inherit" />
                                                        </Avatar>
                                                    </Grid>
                                                </Grid>
                                            </Grid>
                                        </Grid>
                                    </Grid>
                                    <Grid item>
                                        <Typography variant="subtitle2" className={classes.successDark}>
                                            10% Aprovações
                                        </Typography>
                                    </Grid>
                                </Grid>
                            </Grid>

                  
                </Grid>
            </CardContent>
            <CardActions className={classes.cardAction}>
                <Button size="small" color="primary" disableElevation>
                    Ver todos
                    <ChevronRightOutlinedIcon />
                </Button>
            </CardActions>
        </Card>
    );
};

export default PopularCard;
