import React,{ useState, useEffect } from 'react';
import { Grid } from '@material-ui/core';
import { MoonLoader } from 'react-spinners'; // Importe o componente BounceLoader

import { gridSpacing } from '../../store/constant';
import EarningCard from '../../ui-component/cards/EarningCard';
import TotalChartCard from '../../ui-component/cards/TotalChartCard';
import TotalIncomePatternCard from '../../ui-component/cards/TotalIncomePatternCard';
import TotalIncomeCard from '../../ui-component/cards/TotalIncomeCard';
import ChartCard from '../../ui-component/cards/ChartCard';
import PopularCard from '../../ui-component/cards/PopularCard';
import authService from '../../utils/services/auth';

let usuario=null
const HomePage = () => {

  const [carregando, setCarregando] = useState(true);


    useEffect(() => {
      usuario =  authService.getCurrentUser()
      setTimeout(() => {
        setCarregando(false);
      }, 2000);

      }, [])

      
    return (
        <Grid container >
            
        {carregando ? (
            <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', width: '100%',marginTop:'20%' }}>
             <MoonLoader color="#36d7b7"  />
            </div>
      
        ) : (
          usuario.authorities[0] === 'ESTUDANTE' ? 'Seja Bem vindo caro estudante!' : 
          usuario.authorities[0] === 'PROFESSOR' ? 'Seja Bem vindo caro Professor!' :
          usuario.authorities[0] === 'DPT' ? 'Seja Bem vindo caro Chefe do Departamento!' :
          usuario.authorities[0] === 'RA_N2' ? 'Seja Bem vindo caro Técnico de Registo Academico!':''
        )}
      </Grid>
    );
};

export default HomePage;
